<template>
    <div class="all_com">
        <div class="title_com">
            <span>用户信息</span>
        </div>
        <offlineUserInfo
                :accountId="accountId">

        </offlineUserInfo>
    </div>
</template>
<script>
    import offlineUserInfo from '../../components/offlineUserInfo.vue'
    export default{
        name: 'offline_user_info',
        data(){
            return {
                accountId:0
            }
        },
        components: {
            offlineUserInfo // 注册子组件
        },
        methods: {
        },
        created(){
            this.accountId = this.$route.query.accountId
            console.log(this.accountId)
        }
    }
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";
</style>




